import Vue from 'vue'
import VueRouter from 'vue-router'
Vue.use(VueRouter)

import Designer from '@/views/designer/index'
import Preview from '@/views/preview/index'


//  router  `routes` 
export default new VueRouter({
  // mode: 'history',
  base: '/designer',
  routes: [
    {
      path: '*',
      redirect: '/design'
    },
    {
      path: '/design',
      name: 'Designer',
      component: Designer
    },
    {
      path: '/preview',
      name: 'preview',
      component: Preview
    }
  ]
})
